import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = {   
    state: {       
        onLoad: false      

    },
    mutations: {
       setTopicId(state, payload) {
            state.topic_id = payload.topic_Id;
        }
    }
};

export default new Vuex.Store(store);
